
:root {
  --font-color: #2c3e50;
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--font-color);
  width: 100%;
  min-height: 100vh;
}
.row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row;
          flex-flow: row;
}
.column {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column;
          flex-flow: column;
}
.width_100 {
  width: 100%;
}
.section__inner {
  padding: 3rem;
  margin-top: 5rem;
  padding-left: auto;
}
/* navbar */
.navbar-area {
  margin: 0;
  padding: .5rem 3rem;
  padding-right: 6rem;
  list-style-type: none;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: fixed;
  width: 100%;
  z-index: 2;
}
.navbar-area.scrolled {
  background-color: aliceblue;
  font-weight: 500;
}
.navbar-area .app-logo {
  font-family: 'Sacramento', cursive;
  font-size: 4rem !important;
  padding-top: 1rem;
  margin-right: auto;
  color: var(--font-color);
  text-decoration: none;
}
.app-logo:hover {
  color: #888888;
}
.navbar-area ul,
.navbar-area li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.navbar-items {
  margin-left: 3rem;
  padding-top: 2rem;
  display: block;
  text-decoration: none;
  font-size: 1rem;
}
.navbar-items:first-of-type {
  margin-left: 2rem;
}
.navbar-link {
  text-decoration: none;
  color: var(--font-color);
}
/* footer */
.footer-area {
  height: auto;
}
.footer-area .section__inner {
  margin-left: auto;
}
.footer-social {
  text-align: right;
  padding: 0 3.5rem;
}
.footer-social li {
  display: inline-block;
  margin-right: 10px;
}
.footer-social a {
  color: rgba(0,0,0,0.5);
}
.subtitle {
  font-size: 2rem;
}
.work .thumbnail-content .summary a {
  color: var(--font-color);
}
.work a.dribbbleLink {
  color: #e93e93 !important;
}
/*=== animation for navbar-items ===*/
@media only screen and (max-width: 767px) {
.navbar-items li.active a,
  .navbar-link.visited,
  .navbar-link:active,
  .navbar-link:focus,
  .footer-social li.active a,
  .footer-social a:active,
  .footer-social a:focus {
    font-weight: 900;
    color: rgba(0,0,0,0.7);
}
}
@media only screen and (min-width: 767px) {
.navbar-link,
  .footer-social a {
    color: rgba(0, 0, 0, 0.5);
    font-size: 18px;
    padding: 10px;
    position: relative;
    -webkit-transition: 0.2s;
    transition: 0.2s;
}
.navbar-link:after,
  .footer-social a:after {
    content: "";
    position: absolute;
    height: 2px;
    bottom: 7px;
    left: 10px;
    right: 10px;
    background: rgba(0, 0, 0, 0.6);
    visibility: hidden;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
.navbar-items li.active a,
  .footer-social li.active a {
    text-decoration: none;
    color: rgba(0, 0, 0, 0.6);
}
.navbar-link:hover:after,
  .navbar-link.visited:after,
  .navbar-items li.active a:after,
  .footer-social a:hover:after,
  .footer-social li.active a:after {
    visibility: visible;
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
}
.navbar-link:active,
  .navbar-link:focus,
  .navbar-link.visited,
  .footer-social a:active,
  .footer-social a:focus {
    outline: none;
    text-decoration: none;
}
}
/*=== responsive for navbar ===*/
@media screen and (max-width: 768px){
.navbar-area {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
.navbar-area .app-logo {
    width: 30rem;
    margin-right: unset;
}
}
@media only screen and (min-width: 480px) and (max-width: 768px) {
.navbar-area {
    margin: 0 auto;
}
.navbar-area .app-logo {
    text-align: center;
}
}
@media only screen and (max-width: 480px) {
.navbar-area .app-logo {
    width: 25rem;
}
.navbar-area nav {
    margin-left: 2rem;
}
}

